.dashboard {
    // padding: 20px 0;

    .dashboard-header {
        background-image: url('../../assets/bg-1.jpg');
        padding: 20px;
        color: #fff;
    }

    .dashboard-content {
        width: 80%;
        margin: auto;

        .dashboard-sidebar {

            .sidebar {
                .sidebar-links {
                    text-align: left;

                    .sidebar-link-col {
                        cursor: pointer;
                        border-bottom: .1rem solid #ebebeb;
                        padding: 5px;
                        font-weight: 300;

                        a {
                            color: #000;
                            font-family: 'Inter', sans-serif;
                        }
                    }

                    .sidebar-link-col-active {
                        border-left: 3px solid #073313;
                        font-weight: 500;
                        background-color: #C7F3D3;
                    }

                    .sidebar-link-col:hover {
                        background-color: #C7F3D3;

                        a {
                            color: #073313;

                            span {
                                color: #073313;
                            }
                        }
                    }
                }
            }
        }

        .admin-dashboard {
            text-align: left;

            .dashboard-title {
                p {
                    color: #bbb9b9;
                }
            }

            .dashboard-target {
                // background-image: url('../../assets/dashboard-bg.jpg');
                background-color: rgba(199, 243, 211, 0.6);
                background-size: cover;
            }

            .dashboard-card {
                box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;

                .icon-container {
                    font-size: 21px;
                    border-radius: 50%;
                    width: 50px;
                    height: 50px;
                    display: flex;
                    justify-content: center;
                    color: #fff;
                    align-items: center;
                }

                .desc-container {
                    p {
                        font-size: 16px;
                    }

                    small {
                        color: #bbb9b9;

                    }
                }
            }

            .dashboard-card-title {
                display: flex;
                justify-content: space-between;
                align-items: center;
            }

            .dashboard-card-more {
                background-color: #eeeaea;
            }

            .top-deals-card {

                h4 {
                    margin: 0;
                }
            }
        }

        //logs
        .log-item {
            box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
            border-radius: 10px;
            padding: 10px;

            .log-icon {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 100%;
            }

            .log-amount, .log-actions {
                display: flex;
                align-items: center;
                height: 100%;
            }

            .log-detail-col {
                text-align: left;
            }

            .log-amount {
                flex-direction: column;
            }

            .log-actions {
                button:first-child {
                    margin-right: 10px;
                }
            }
        }

        .log-filter {
            display: flex;
            align-items: center;

            button:nth-child(even) {
                margin: 0 10px;
            }

            .log-filter-active {
                color: #073313;
                border: 1px solid #073313;
            }
        }
        //transactions
        .transaction-item {
            box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
            border-radius: 10px;
            padding: 10px;

            .transaction-icon {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 100%;
            }

            .transaction-amount {
                display: flex;
                align-items: center;
                height: 100%;
            }

            .transaction-detail-col {
                text-align: left;
            }

            .transaction-amount {
                flex-direction: column;
            }
        }
        .transaction-filter {
            display: flex;
            align-items: center;

            button:nth-child(even) {
                margin: 0 10px;
            }

            .transaction-filter-active {
                color: #073313;
                border: 1px solid #073313;
            }
        }
    }
}